\"displayname\"\r\n\t\t\t\t\ttype=\"text\">\r\n\t\t\t\t<input id=\"newuserpassword\"\r\n\t\t\t\t\tref=\"newuserpassword\"\r\n\t\t\t\t\tv-model=\"newUser.password\"\r\n\t\t\t\t\t:minlength=\"minPasswordLength\"\r\n\t\t\t\t\t:maxlength=\"469\"\r\n\t\t\t\t\t:placeholder=\"t('settings', 'Password')\"\r\n\t\t\t\t\t:required=\"newUser.mailAddress===''\"\r\n\t\t\t\t\tautocapitalize=\"none\"\r\n\t\t\t\t\tautocomplete=\"new-password\"\r\n\t\t\t\t\tautocorrect=\"off\"\r\n\t\t\t\t\tclass=\"modal__item\"\r\n\t\t\t\t\tname=\"password\"\r\n\t\t\t\t\ttype=\"password\">\r\n\t\t\t\t<input id=\"newemail\"\r\n\t\t\t\t\tv-model=\"newUser.mailAddress\"\r\n\t\t\t\t\t:placeholder=\"t('settings', 'Email')\"\r\n\t\t\t\t\t:required=\"newUser.password==='' || settings.newUserRequireEmail\"\r\n\t\t\t\t\tautocapitalize=\"none\"\r\n\t\t\t\t\tautocomplete=\"off\"\r\n\t\t\t\t\tautocorrect=\"off\"\r\n\t\t\t\t\tclass=\"modal__item\"\r\n\t\t\t\t\tname=\"email\"\r\n\t\t\t\t\ttype=\"email\">\r\n\t\t\t\t<div class=\"groups modal__item\">\r\n\t\t\t\t\t<!-- hidden input trick for vanilla html5 form validation -->\r\n\t\t\t\t\t<input v-if=\"!settings.isAdmin\"\r\n\t\t\t\t\t\tid=\"newgroups\"\r\n\t\t\t\t\t\t:class=\"{'icon-loading-small': loading.groups}\"\r\n\t\t\t\t\t\t:required=\"!settings.isAdmin\"\r\n\t\t\t\t\t\t:value=\"newUser.groups\"\r\n\t\t\t\t\t\ttabindex=\"-1\"\r\n\t\t\t\t\t\ttype=\"text\">\r\n\t\t\t\t\t<NcMultiselect v-model=\"newUser.groups\"\r\n\t\t\t\t\t\t:close-on-select=\"false\"\r\n\t\t\t\t\t\t:disabled=\"loading.groups||loading.all\"\r\n\t\t\t\t\t\t:multiple=\"true\"\r\n\t\t\t\t\t\t:options=\"canAddGroups\"\r\n\t\t\t\t\t\t:placeholder=\"t('settings', 'Add user to group')\"\r\n\t\t\t\t\t\t:tag-width=\"60\"\r\n\t\t\t\t\t\t:taggable=\"true\"\r\n\t\t\t\t\t\tclass=\"multiselect-vue\"\r\n\t\t\t\t\t\tlabel=\"name\"\r\n\t\t\t\t\t\ttag-placeholder=\"create\"\r\n\t\t\t\t\t\ttrack-by=\"id\"\r\n\t\t\t\t\t\t@tag=\"createGroup\">\r\n\t\t\t\t\t\t<!-- If user is not admin, he is a subadmin.\r\n\t\t\t\t\t\t\tSubadmins can't create users outside their groups\r\n\t\t\t\t\t\t\tTherefore, empty select is forbidden -->\r\n\t\t\t\t\t\t<span slot=\"noResult\">{{ t('settings', 'No results') }}</span>\r\n\t\t\t\t\t</NcMultiselect>\r\n\t\t\t\t</div>\r\n\t\t\t\t<div v-if=\"subAdminsGroups.length>0 && settings.isAdmin\"\r\n\t\t\t\t\tclass=\"subadmins modal__item\">\r\n\t\t\t\t\t<NcMultiselect v-model=\"newUser.subAdminsGroups\"\r\n\t\t\t\t\t\t:close-on-select=\"false\"\r\n\t\t\t\t\t\t:multiple=\"true\"\r\n\t\t\t\t\t\t:options=\"subAdminsGroups\"\r\n\t\t\t\t\t\t:placeholder=\"t('settings', 'Set user as admin for')\"\r\n\t\t\t\t\t\t:tag-width=\"60\"\r\n\t\t\t\t\t\tclass=\"multiselect-vue\"\r\n\t\t\t\t\t\tlabel=\"name\"\r\n\t\t\t\t\t\ttrack-by=\"id\">\r\n\t\t\t\t\t\t<span slot=\"noResult\">{{ t('settings', 'No results') }}</span>\r\n\t\t\t\t\t</NcMultiselect>\r\n\t\t\t\t</div>\r\n\t\t\t\t<div class=\"quota modal__item\">\r\n\t\t\t\t\t<NcMultiselect v-model=\"newUser.quota\"\r\n\t\t\t\t\t\t:allow-empty=\"false\"\r\n\t\t\t\t\t\t:options=\"quotaOptions\"\r\n\t\t\t\t\t\t:placeholder=\"t('settings', 'Select user quota')\"\r\n\t\t\t\t\t\t:taggable=\"true\"\r\n\t\t\t\t\t\tclass=\"multiselect-vue\"\r\n\t\t\t\t\t\tlabel=\"label\"\r\n\t\t\t\t\t\ttrack-by=\"id\"\r\n\t\t\t\t\t\t@tag=\"validateQuota\" />\r\n\t\t\t\t</div>\r\n\t\t\t\t<div v-if=\"showConfig.showLanguages\" class=\"languages modal__item\">\r\n\t\t\t\t\t<NcMultiselect v-model=\"newUser.language\"\r\n\t\t\t\t\t\t:allow-empty=\"false\"\r\n\t\t\t\t\t\t:options=\"languages\"\r\n\t\t\t\t\t\t:placeholder=\"t('settings', 'Default language')\"\r\n\t\t\t\t\t\tclass=\"multiselect-vue\"\r\n\t\t\t\t\t\tgroup-label=\"label\"\r\n\t\t\t\t\t\tgroup-values=\"languages\"\r\n\t\t\t\t\t\tlabel=\"name\"\r\n\t\t\t\t\t\ttrack-by=\"code\" />\r\n\t\t\t\t</div>\r\n\t\t\t\t<div v-if=\"showConfig.showStoragePath\" class=\"storageLocation\" />\r\n\t\t\t\t<div v-if=\"showConfig.showUserBackend\" class=\"userBackend\" />\r\n\t\t\t\t<div v-if=\"showConfig.showLastLogin\" class=\"lastLogin\" />\r\n\t\t\t\t<div class=\"user-actions\">\r\n\t\t\t\t\t<NcButton id=\"newsubmit\"\r\n\t\t\t\t\t\ttype=\"primary\"\r\n\t\t\t\t\t\tnative-type=\"submit\"\r\n\t\t\t\t\t\tvalue=\"\">\r\n\t\t\t\t\t\t{{ t('settings', 'Add a new user') }}\r\n\t\t\t\t\t</NcButton>\r\n\t\t\t\t</div>\r\n\t\t\t</form>\r\n\t\t</NcModal>\r\n\t\t<div id=\"grid-header\"\r\n\t\t\t:class=\"{'sticky': scrolled && !showConfig.showNewUserForm}\"\r\n\t\t\tclass=\"row\">\r\n\t\t\t<div id=\"headerAvatar\" class=\"avatar\" />\r\n\t\t\t<div id=\"headerName\" class=\"name\">\r\n\t\t\t\t{{ t('settings', 'Username') }}\r\n\r\n\t\t\t\t<div class=\"subtitle\">\r\n\t\t\t\t\t{{ t('settings', 'Display name') }}\r\n\t\t\t\t</div>\r\n\t\t\t</div>\r\n\t\t\t<div id=\"headerPassword\" class=\"password\">\r\n\t\t\t\t{{ t('settings', 'Password') }}\r\n\t\t\t</div>\r\n\t\t\t<div id=\"headerAddress\" class=\"mailAddress\">\r\n\t\t\t\t{{ t('settings', 'Email') }}\r\n\t\t\t</div>\r\n\t\t\t<div id=\"headerGroups\" class=\"groups\">\r\n\t\t\t\t{{ t('settings', 'Groups') }}\r\n\t\t\t</div>\r\n\t\t\t<div v-if=\"subAdminsGroups.length>0 && settings.isAdmin\"\r\n\t\t\t\tid=\"headerSubAdmins\"\r\n\t\t\t\tclass=\"subadmins\">\r\n\t\t\t\t{{ t('settings', 'Group admin for') }}\r\n\t\t\t</div>\r\n\t\t\t<div id=\"headerQuota\" class=\"quota\">\r\n\t\t\t\t{{ t('settings', 'Quota') }}\r\n\t\t\t</div>\r\n\t\t\t<div v-if=\"showConfig.showLanguages\"\r\n\t\t\t\tid=\"headerLanguages\"\r\n\t\t\t\tclass=\"languages\">\r\n\t\t\t\t{{ t('settings', 'Language') }}\r\n\t\t\t</div>\r\n\r\n\t\t\t<div v-if=\"showConfig.showUserBackend || showConfig.showStoragePath\"\r\n\t\t\t\tclass=\"headerUserBackend userBackend\">\r\n\t\t\t\t<div v-if=\"showConfig.showUserBackend\" class=\"userBackend\">\r\n\t\t\t\t\t{{ t('settings', 'User backend') }}\r\n\t\t\t\t</div>\r\n\t\t\t\t<div v-if=\"showConfig.showStoragePath\"\r\n\t\t\t\t\tclass=\"subtitle storageLocation\">\r\n\t\t\t\t\t{{ t('settings', 'Storage location') }}\r\n\t\t\t\t</div>\r\n\t\t\t</div>\r\n\t\t\t<div v-if=\"showConfig.showLastLogin\"\r\n\t\t\t\tclass=\"headerLastLogin lastLogin\">\r\n\t\t\t\t{{ t('settings', 'Last login') }}\r\n\t\t\t</div>\r\n\r\n\t\t\t<div class=\"userActions\" />\r\n\t\t</div>\r\n\r\n\t\t<user-row v-for=\"user in filteredUsers\"\r\n\t\t\t:key=\"user.id\"\r\n\t\t\t:external-actions=\"externalActions\"\r\n\t\t\t:groups=\"groups\"\r\n\t\t\t:languages=\"languages\"\r\n\t\t\t:quota-options=\"quotaOptions\"\r\n\t\t\t:settings=\"settings\"\r\n\t\t\t:show-config=\"showConfig\"\r\n\t\t\t:sub-admins-groups=\"subAdminsGroups\"\r\n\t\t\t:user=\"user\"\r\n\t\t\t:is-dark-theme=\"isDarkTheme\" />\r\n\t\t<InfiniteLoading ref=\"infiniteLoading\" @infinite=\"infiniteHandler\">\r\n\t\t\t<div slot=\"spinner\">\r\n\t\t\t\t<div class=\"users-icon-loading icon-loading\" />\r\n\t\t\t</div>\r\n\t\t\t<div slot=\"no-more\">\r\n\t\t\t\t<div class=\"users-list-end\" />\r\n\t\t\t</div>\r\n\t\t\t<div slot=\"no-results\">\r\n\t\t\t\t<div id=\"emptycontent\">\r\n\t\t\t\t\t<div class=\"icon-contacts-dark\" />\r\n\t\t\t\t\t<h2>{{ t('settings', 'No users in here') }}</h2>\r\n\t\t\t\t</div>\r\n\t\t\t</div>\r\n\t\t</InfiniteLoading>\r\n\t</div>\r\n</template>\r\n\r\n<script>\r\nimport { subscribe, unsubscribe } from '@nextcloud/event-bus'\r\nimport InfiniteLoading from 'vue-infinite-loading'\r\nimport Vue from 'vue'\r\nimport NcModal from '@nextcloud/vue/dist/Components/NcModal'\r\nimport NcButton from '@nextcloud/vue/dist/Components/NcButton'\r\nimport NcMultiselect from '@nextcloud/vue/dist/Components/NcMultiselect'\r\n\r\nimport userRow from './UserList/UserRow'\r\n\r\nconst unlimitedQuota = {\r\n\tid: 'none',\r\n\tlabel: t('settings', 'Unlimited'),\r\n}\r\nconst defaultQuota = {\r\n\tid: 'default',\r\n\tlabel: t('settings', 'Default quota'),\r\n}\r\nconst newUser = {\r\n\tid: '',\r\n\tdisplayName: '',\r\n\tpassword: '',\r\n\tmailAddress: '',\r\n\tgroups: [],\r\n\tsubAdminsGroups: [],\r\n\tquota: defaultQuota,\r\n\tlanguage: {\r\n\t\tcode: 'en